.backToPacks {
  padding: 0 140px;
  position: absolute;
  width: 200px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  margin: 140px auto;
  padding: 60px;

  .title {
    margin: 0 0 30px;
    text-align: center;
  }

  .ava {
    position: relative;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    .buttonWrapper {
      position: absolute;
      bottom: 0;
      right: 0;
      color: white;
      border: 1px #ffffff solid;
      border-radius: 50%;
      background-color: #808080;

      .button {
        width: 30px;
        height: 30px;
      }
    }
  }

  .email {
    margin-bottom: 29px;
  }

  .logout {
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3);
    filter: drop-shadow(0px 2px 10px rgba(109, 109, 109, 0.25));

    .text {
      position: relative;
      bottom: -1px;
    }
  }
}
